/* Start discount */
.discount {
   min-height: 100vh;
   display: flex;
   flex-wrap: wrap;
   padding-bottom: 50px;
}

.discount .image {
   background-image: url(../../utils/images/a.png);
   background-size: cover;
   color: white;
   flex-basis: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
   position: relative;
   z-index: 1;
   animation: change-background 10s linear infinite;
}

.discount .image::before {
   content: "";
   position: absolute;
   left: 0;
   top: 0;
   width: 100%;
   height: 100%;
   background-color: rgb(23 135 224 / 95%);
   z-index: -1;
}

@media (max-width: 991px) {
   .discount .image {
      flex-basis: 100%;
   }
}

.discount .form {
   display: flex;
   align-items: center;
   justify-content: center;
   flex-basis: 50%;
}

@media (max-width: 991px) {
   .discount .form {
      flex-basis: 100%;
   }
}

.discount .form .input {
   display: block;
   width: 100%;
   margin-bottom: 25px;
   padding: 15px;
   border: none;
   border-bottom: 1px solid #ccc;
   background-color: #f9f9f9;
   caret-color: var(--main-color);
}

.discount .form textarea.input {
   resize: none;
   height: 200px;
}

.discount .form .input:focus {
   outline: none;
}

.discount .form [type="submit"] {
   display: block;
   width: 100%;
   padding: 15px;
   background-color: var(--main-color);
   color: white;
   font-weight: bold;
   font-size: 20px;
   border: none;
   cursor: pointer;
   transition: var(--main-transition);
}

.discount .form [type="submit"]:hover {
   background-color: var(--main-color-alt);
}

.discount .content {
   text-align: center;
   padding: 0 20px;
}

.discount .content h2 {
   font-size: 40px;
   letter-spacing: -2px;
}

.discount .content p {
   line-height: 1.6;
   font-size: 18px;
   max-width: 500px;
}

.discount .content img {
   width: 300px;
   max-width: 100%;
}

/* End discount */